.commonpage-container {
  padding: 10px;
}

.common-page {
  display: flex; 
  margin: 40px auto 100px;
  border: 1px solid #ccc;
  max-width: 1200px;
}

.common-page:first-child {
  margin-top: 80px;
}

.description-btn-container {
  width: 100%;
  height: 65px;
}

.btn-cont {
  display: inline-block;
  text-align: center;
  margin-top: 20px;
}

.btn-cont:first-child {
  margin-right: 30px;
}

.btn-cont .btn {
  position: relative;
  padding: 20px 60px;
  border: 1px solid #011C26;
  color: #011C26;
  text-decoration: none;
  font-size: em(18px);
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  -webkit-font-smoothing: antialiased;
}

.btn-cont .btn:hover {
  border: 1px solid transparent;
}

.btn-cont .btn:hover .line-1 {
  animation: move1 1500ms infinite ease;
}

.btn-cont .btn:hover .line-2 {
  animation: move2 1500ms infinite ease;
}

.btn-cont .btn:hover .line-3 {
  animation: move3 1500ms infinite ease;
}

.btn-cont .btn:hover .line-4 {
  animation: move4 1500ms infinite ease;
}

.btn-cont .line-1 {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  background-color: #011C26;
  left: 0;
  bottom: 0;
}

.btn-cont .line-2 {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  background-color: #011C26;
  left: 0;
  top: 0;
}

.btn-cont .line-3 {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  background-color: #011C26;
  right: 0;
  top: 0;
}

.btn-cont .line-4 {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  background-color: #011C26;
  right: 0;
  bottom: 0;
}

@media (max-width: 1200px) {
  .common-page {
    flex-direction: column;
    max-width: 700px;
    margin: 40px auto 80px;
  }

  .common-page .description-container {
    margin-top: 0px;
  }

  .common-page .description {
    padding: 0px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .description-btn-container {
    justify-content: center;
    text-align: center;
  }

  .common-page h1 {
    font-size: 32px;
    text-align: center;
  }
}

@media (max-width: 686px) {
  .common-page {
    width: 100%;
    margin: 40px auto;
  }
}

@media (max-width: 530px) {
  .description-btn-container {
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    height: 160px;
  }
  
  .btn-cont,
  .btn-cont:first-child {
    display: block;
    width: 100%;
    margin: 0;
  }
  
  .btn-cont .btn {
    box-sizing: border-box;
    display: block;
    width: 100%;
  }
  
  .btn-cont:first-child {
    margin-bottom: 30px;
  }
}

@keyframes move1 {
  0% {
    height: 100%;
    bottom: 0;
  }
  54% {
    height: 0;
    bottom: 100%;
  }
  55% {
    height: 0;
    bottom: 0;
  }
  100% {
    height: 100%;
    bottom: 0;
  }
}
@keyframes move2 {
  0% {
    width: 0;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes move3 {
  0% {
    height: 100%;
    top: 0;
  }
  54% {
    height: 0;
    top: 100%;
  }
  55% {
    height: 0;
    top: 0;
  }
  100% {
    height: 100%;
    top: 0;
  }
}
@keyframes move4 {
  0% {
    width: 0;
    right: 0;
  }
  55% {
    width: 100%;
    right: 0;
  }
  100% {
    width: 0;
    right: 100%;
  }
}
