.header-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.logo {
  width: 200px;
  border-radius: 50%;
  border: 1px solid #ccc;
}

.header-title-container {
  text-align: center;
}

.header-title {
  margin: 0;
  color: #011C26;
  font-size: 44px;
  line-height: 60px;
  font-weight: 700;
}

.header-subtitle {
  display: block;
  font-size: 24px;
  padding: 10px;
  text-decoration: none;
  margin: 0;
  color: black;
}

.header-subtitle:hover {
  text-decoration: underline;
}

.header-links ul {
  display: flex;
  margin: 0;
}

.header-links ul li {
  list-style: none;
  min-width: 200px;
  text-align: right;
}

.header-link {
  color: #000;
  font-size: 24px;
  line-height: 80px;
  font-weight: 700;
  text-decoration: none;
}

@media (max-width: 575px) {
  .header-container {
    flex-direction: column;
  }
}

@media (max-width: 375px) {
  .header-title {
    font-size: 32px;
  }

  .header-subtitle {
    font-size: 22px;
  }

}