footer {  
  position: relative;
  text-align: center;
  border-top: 1px solid #ccc;
  padding-top: 10px;
  margin: 1px auto;
  color: #fff;
  background-color: #011C26;
}

.footer-container a {
  text-decoration: none;
  color: #fff;
  margin: 20px 0 ;
}

.footer-container a:hover {
  text-decoration: underline;
}

.footer-container {
  width: 100%;
  max-width: 1200px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .footer-container {
    flex-direction: column;
    max-width: 586px;
  }
}