.preview-container {
  margin: 40px;
  width: 506px;
  height: 280px;
}

.preview-container img {
  width: 506px;
  height: 280px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.225);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  border: 1px solid #fff;
}

.preview-container img:hover {
  box-shadow: 0 4px 10px 0px rgba(0,0,0,0.225);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}

@media (max-width: 1200px) {
  .preview-container {
    margin: 40px auto;
  }
}

@media (max-width: 686px) {
  .preview-container {
    width: calc(100% - 80px);    
    height: 280px;
    margin: 40px auto;
  }
  
  .preview-container img {
    width: 100%;
    height: 280px;
    object-fit: cover;
  }
  
}

@media (max-width: 580px) {
  .preview-container {
    width: calc(100% - 40px);
    height: 270px;
    margin: 20px auto;
  } 
    
  .preview-container img {
    height: 270px;
  }
  
}

@media (max-width: 480px) {
  .preview-container {
    width: calc(100% - 40px);
    height: 200px;
    margin: 20px auto;
  } 
    
  .preview-container img {
    height: 200px;
  }  
}

@media (max-width: 375px) {
  .preview-container {
    height: 170px;
  } 
    
  .preview-container img {
    height: 170px;
  }  
}

@media (max-width: 320px) {
  .preview-container {
    height: 150px;
  } 
    
  .preview-container img {
    height: 150px;
  }  
}